import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: () => import('../views/index.vue'),
    },
    {
      path: '/renyuanguanli',
      name: 'renyuanguanli',
      component: () => import('../views/renyuanguanli/index.vue'),
    },
    {
      path: '/gongtongfuyu',
      name: 'gongtongfuyu',
      component: () => import('../views/gongtongfuyu/index.vue'),
    },
    {
      path: '/zhilifuwu',
      name: 'zhilifuwu',
      component: () => import('../views/zhilifuwu/index.vue'),
    }
  ]
})



